.page {
  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
      overflow: hidden;
    }

    @include on-small-desktop {
      display: block;
      position: static;
      opacity: 1;
      transform: none;
      pointer-events: auto;
    }
  }
}
